import React, { useState } from "react";
import { getAllBook } from "../api/bookmark";
import { getTag } from "../api/tag";


function GetAll() {

    const [ans, setAns] = useState([]);
    const [tags, setTags] = useState([]);

    function book() {
        getAllBook(result => {
            setAns([].concat(result));
            console.log(ans);
        })
    }
    function meth2(e) {
        return (
            <p>{e._id}</p>
        )
    }
    function meth(e) {
        return (<tr>
            <td>{e.Title}</td>
            <td>{e.Link}</td>
            <td>{e.Publisher}</td>
            <td>{e.TimeUpdated}</td>
            <td>{e.TimeCreated}</td>
            {e.Tags.map(meth2)}
        </tr>
        )
    }
    function tag() {
        getTag(result => {
            setTags([].concat(result));
        })
    }
    function renTags(e) {
        return (
            <tr>
                <td>{e.Title}</td>
                <td>{e.TimeUpdated}</td>
                <td>{e.TimeCreated}</td>
            </tr>

        )
    }
    function reset() {
        setAns([]);
        setTags([]);
    }
    return (
        <div>
            <button className="btn btn-lg btn-primary" onClick={book}>Get All Bookmarks</button><span style={{ marginLeft: 20 }} />
            <button className="btn btn-lg btn-dark" onClick={tag}>Gett All Tags</button><span style={{ marginLeft: 20 }} />
            <button className="btn btn-sm btn-danger" onClick={reset}>Reset</button>
            <br /> <br />
            {ans.length !== 0 ?
                <table>
                    <tr>
                        <th>Title</th>
                        <th>Link</th>
                        <th>Publisher</th>
                        <th>Time Created</th>
                        <th>Time Updated</th>
                        <th>Tags Id</th>
                    </tr>
                    {ans.map(meth)}
                </table>
                : null
            }
            <div>
                <br /> <br />
                {tags.length !== 0 ?
                    <table>
                        <tr>
                            <th>Title</th>
                            <th>Time Created</th>
                            <th>Time Updated</th>
                        </tr>
                        {tags.map(renTags)}
                    </table>
                    : null}
            </div>


        </div>
    )
}

export default GetAll;