import React, { useState } from "react";
import styled, { keyframes } from 'styled-components';
import { fadeInRight, fadeInLeft } from 'react-animations';
import { addTag, deleteTag } from "../api/tag";



const Left = styled.div`animation: 2s ${keyframes`${fadeInLeft}`} `;
const Right = styled.div`animation: 2s ${keyframes`${fadeInRight}`} `;

function Tags() {
    const [title, setTitle] = useState("");
    const [deltitle, setDel] = useState("");
    const [err, setErr] = useState({
        title: "",
        del: ""
    })
    const [ans, setAns] = useState({
        title: "",
        del: ""
    })
    function changeTitle(e) {
        var val = e.target.value;
        setTitle(val);
    }
    function stateRe() {
        setErr({
            title: "",
            del: ""
        })
        setAns({
            title: "",
            del: ""
        })
    }
    function subTitle() {
        stateRe();
        var data = {
            title
        }
        addTag(data, (result) => {
            if (result.err) {
                setErr(prev => ({
                    ...prev,
                    title: result.err
                }))
            } else {
                setAns(prev => ({
                    ...prev,
                    title: result.success
                }))
            }
        })
    }
    function delChange(e) {
        var val = e.target.value;
        setDel(val);
    }
    function delTag() {
        stateRe();
        var data = {
            name: deltitle
        }
        deleteTag(data, result => {
            if (result.err) {
                setErr(prev => ({
                    ...prev,
                    del: result.err
                }))
            } else {
                setAns(prev => ({
                    ...prev,
                    del: result.success
                }))
            }
        })
    }
    return (
        <div>
            <Right><h3>a) Add Tag</h3></Right>
            <Left>
                <input type="text" className="form-control" onChange={changeTitle} placeholder="Title of the Tag" /><br />
                {err.title ? <div><small style={{ color: "red" }}>{err.title}</small><br /><br /></div> : null}
                {ans.title ? <div><small style={{ color: "green" }}>{ans.title}</small><br /><br /></div> : null}
                <div className="flex"><button className="btn btn-lg btn-success" onClick={subTitle}>Add Tag</button><br /></div>
            </Left>
            <br /><br />
            <Right><h3>b)Delete Tag</h3></Right>
            <Left>
                <input type="text" placeholder="Enter tag Name" className="form-control" onChange={delChange} /><br />
                {err.del ? <div><small style={{ color: "red" }}>{err.del}</small><br /><br /></div> : null}
                {ans.del ? <div><small style={{ color: "green" }}>{ans.del}</small><br /><br /></div> : null}
                <div className="flex">     <button className="btn btn-lg btn-danger" onClick={delTag}>Delete Tag</button></div>
            </Left>
            <br /><br />
        </div>
    )
}

export default Tags;