import axios from "axios";

export const addTag = (data, clbk) => {

    axios.post("/addTag", data).then(result => {
        console.log(result);
        clbk(result.data);
    })
}

export const addTagToBook = (data, clbk) => {

    axios.post("/addTagToBookmark", data).then(result => {
        console.log(result);
        clbk(result.data);
    })
}

