import axios from "axios";

export const addBookmark = (data, clbk) => {

    axios.post("/addBookmark", data).then(result => {
        console.log(result);
        clbk(result.data);
    })
}

export const deleteBookmark = (data, clbk) => {

    axios.post("/deleteBookmark", data).then(result => {
        console.log(result);
        clbk(result.data);
    })
}


export const getAllBook = (clbk) => {

    axios.get("/getBookmarks").then(result => {
        console.log(result);
        clbk(result.data);
    })
}