import React, { useState } from "react";
import styled, { keyframes } from 'styled-components';
import { fadeInDownBig, fadeInRight, fadeInLeft } from 'react-animations';
import { addTagToBook, removeTag } from "../api/tag";


const Bounce = styled.div`animation: 2s ${keyframes`${fadeInDownBig}`} `;
const Left = styled.div`animation: 2s ${keyframes`${fadeInLeft}`} `;
const Right = styled.div`animation: 2s ${keyframes`${fadeInRight}`} `;


function TagsToBook() {
    const [add, setAdd] = useState({
        tagName: "",
        bookmarkName: ""
    })

    const [err, setErr] = useState({
        add: "",
        remove: ""
    })
    const [ans, setAns] = useState({
        add: "",
        remove: ""
    })

    function addChange(e) {
        const { id, value } = e.target;
        setAdd(prev => ({
            ...prev,
            [id]: value
        }))
    }
    function stateRes() {
        setAdd({
            tagName: "",
            bookmarkName: ""
        })
        setAns({
            add: "",
            remove: ""
        })
    }
    function subAdd() {
        stateRes()
        addTagToBook(add, result => {
            if (result.err) {
                setErr(prev => ({
                    ...prev,
                    add: result.err
                }))
            } else {
                setAns(prev => ({
                    ...prev,
                    add: result.success
                }))
            }
        })
    }
    function subRem() {
        stateRes();
        removeTag(add, result => {
            if (result.err) {
                setErr(prev => ({
                    ...prev,
                    remove: result.err
                }))
            } else {
                setAns(prev => ({
                    ...prev,
                    remove: result.success
                }))
            }
        })

    }
    return (
        <div>
            <Left><h3>c) Add a Tag to a Bookmark</h3></Left>
            <Right>
                <input type="text" placeholder="Enter the Book Mark Name" id="bookmarkName" className="form-control" onChange={addChange} /><br />
                <input type="text" placeholder="Enter the Tag name" id="tagName" className="form-control" onChange={addChange} />
                {err.add ? <div><small style={{ color: "red" }}>{err.add}</small><br /><br /></div> : null}
                {ans.add ? <div><small style={{ color: "green" }}>{ans.add}</small><br /><br /></div> : null}<br />
                <div className="flex">    <button className="btn btn-lg btn-warning" onClick={subAdd}>Add Tag to Bookmark </button></div>
            </Right>
            <br />
            <br />
            <Bounce><h3>d) Remove Tag from a Bookmark</h3></Bounce>
            <Left>
                <input type="text" placeholder="Enter the Book Mark Name" id="bookmarkName" className="form-control" onChange={addChange} /><br />
                <input type="text" placeholder="Enter the Tag name" id="tagName" className="form-control" onChange={addChange} />
                {err.remove ? <div><small style={{ color: "red" }}>{err.remove}</small><br /><br /></div> : null}
                {ans.remove ? <div><small style={{ color: "green" }}>{ans.remove}</small><br /><br /></div> : null}<br />
                <div className="flex">      <button className="btn btn-lg btn-danger" onClick={subRem}>Remove Tag from Bookmark </button></div>
            </Left>
        </div>
    )
}

export default TagsToBook;