import React, { useState } from "react";
import styled, { keyframes } from 'styled-components';
import { fadeInRight, fadeInLeft } from 'react-animations';
import { addBookmark, deleteBookmark } from "../api/bookmark";



const Left = styled.div`animation: 2s ${keyframes`${fadeInLeft}`} `;
const Right = styled.div`animation: 2s ${keyframes`${fadeInRight}`} `;



function BookMark() {
    const [addB, setAddB] = useState({
        title: "",
        link: "",
        publisher: ""
    })
    const [delObj, setDel] = useState({
        name: ""
    })
    const [err, setError] = useState("");
    const [ans, setAns] = useState("");
    const [err1, setError1] = useState("");
    const [ans1, setAns1] = useState("");
    function set(result) {
        if (result.err) {
            setError(result.err);
        } else {
            setAns(result.success);
        }
    }
    function changeAddBook(e) {

        const { id, value } = e.target;

        setAddB(prev => ({
            ...prev,
            [id]: value
        }))
        //        console.log(addB);
    }
    function addBook() {
        setError("");
        setAns("");
        addBookmark(addB, (data) => {
            console.log(data);
            set(data);

        })
    }
    function delBook(e) {
        const val = e.target.value;
        setDel({
            name: val
        })
        // console.log(delObj);

    }
    function deleteB() {
        setError("");
        setAns("");
        deleteBookmark(delObj, (result) => {
            if (result.err) {
                setError1(result.err);
            } else {
                setAns1(result.success);
            }
        })
    }
    return (
        <div>
            <Right><h3>a) Add Bookmark</h3></Right>
            <Left>
                <input type="text" className="form-control" id="title" onChange={changeAddBook} placeholder="Enter the title of bookmark" /><br />
                <input type="text" className="form-control" id="link" onChange={changeAddBook} placeholder="Enter the Link" /><br />
                <input type="text" className="form-control" id="publisher" onChange={changeAddBook} placeholder="John Doe" /><br />
                {err ? <div><small style={{ color: "red" }}>{err}</small><br /><br /></div> : null}
                {ans ? <div><small style={{ color: "green" }}>{ans}</small><br /><br /></div> : null}
                <div className="flex">   <button className="btn btn-lg btn-dark" onClick={addBook}>Add BookMark</button></div>
            </Left>
            <br />
            <br />
            <Right><h3>b) Delete Bookmark</h3></Right>
            <Right>
                <input type="text" className="form-control" onChange={delBook} placeholder="Enter the title of bookmark to be deleted" />
                <br />
                {err1 ? <div><small style={{ color: "red" }}>{err1}</small><br /><br /></div> : null}
                {ans1 ? <div><small style={{ color: "green" }}>{ans1}</small><br /><br /></div> : null}

                <div className="flex">     <button className="btn btn-lg btn-danger" onClick={deleteB}>Delete</button> </div>
            </Right>

        </div>
    )
}

export default BookMark;